import { HttpClient } from '@angular/common/http'
import { Component, OnInit } from '@angular/core'
import { Http, Headers } from '@angular/http'
import 'rxjs/add/operator/map'

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.scss']
})
export class AppComponent implements OnInit {
  constructor(private http: Http, private httpClient: HttpClient) {}

  innerHtml(item) {
    const div = document.getElementById('div')
    div.innerHTML += `<p>${item}</p>`
  }

  onClickButton() {
    this.httpGet().subscribe(res => {
      //const testJson = res.json()
      //console.log(testJson)
      console.log(res)
      //this.innerHtml(testJson.data)
      this.innerHtml(res.data)
    })

    this.httpPost().subscribe(res => {
      //const testJson = res.json()
      //console.log(testJson)
      console.log(res)
      //this.innerHtml(testJson.data)
      this.innerHtml(res.data)
    })

    this.android().subscribe(res => {
      //const testJson = res.json()
      //console.log(testJson)
      console.log(res)
      //this.innerHtml(testJson.data)
      //this.innerHtml(res)
    })
    //fetch('http://localhost:8080/homes', {
    fetch('/homes')
      .then(data => data.json())
      .then(data => console.log(data))
  }

  httpGet() {
    const header = new Headers()
    header.append('Content-Type', 'application/json')
    //-------------------------------------------
    // Mobile
    //-----------
    //
    //return this.http.get('https://meanwsbapp.herokuapp.com/home')
    //
    //===========================================
    //-------------------------------------------
    //Server
    //------------
    //
    //return this.http.get('/home')
    //
    //===========================================
    //-------------------------------------------
    //Production
    //
    return this.http
      .post(
        //'http://localhost:8080/homes',
        '/homes',
        { data: 'hello from angular home' },
        { headers: header }
      )
      .map(res => res.json())
  }

  httpPost() {
    const header = new Headers()
    header.append('Content-Type', 'application/json')
    return this.http
      .post(
        //-------------------------------------------
        // Mobile
        //-----------
        //
        //'https://meanwsbapp.herokuapp.com/home',
        //
        //===========================================
        //-------------------------------------------
        //Server
        //------------
        //
        '/home',
        //
        //===========================================
        //-------------------------------------------
        //Production
        //
        //'http://localhost:8080/home',
        //
        //===========================================
        { data: 'hello from angular home' },
        { headers: header }
      )
      .map(res => res.json())
  }

  android() {
    const header = new Headers()
    //header.append('Content-Type', 'application/json')
    return this.http.get(
      //'http://localhost:8080/android',
      '/android' //,
      //{ data: 'hello from angular home' },
      //{ headers: header }
    )
  }

  ngOnInit() {}
}
